<template>
    <section class="light-box">
        <h3 class="section-title" v-if="title">{{title}}</h3>
        <h3 class="site-width section-subtitle">
            <template v-if="subtitle">{{subtitle}}</template>
            <i class="fa fa-angle-down" aria-hidden="true"></i>
        </h3>
        <nav class="site-width thumbnails">
            <figure v-for="(image, index) in images.slice(0, items)"
                :key="index"
                class="thumbnail"
                @click="openGallery(index)">
                <img :src="image.thumb" @error="getDefaultPerson" />
            </figure>
            <figure class="thumbnail view-more" v-if="images.length > items" @click="openGallery(0)">
                <i class="fa fa-plus fa-3x" aria-hidden="true"></i>
                <p>View More</p>
            </figure>
        </nav>
        <lightbox
            :images="images"
            ref="lightbox"
            :show-caption="false"
            :show-light-box="false">
        </lightbox>
    </section>
</template>

<script>
    import Vue from 'vue'
    import VueLazyLoad from 'vue-lazyload'
    import VueTouch from 'vue-touch'
    import Lightbox from 'vue-image-lightbox'

    Vue.use(VueLazyLoad)
    Vue.use(VueTouch, { name: 'v-touch' })

    export default {
        name: 'appLightbox',
        components: {
            'lightbox': Lightbox
        },
        data () {
            return {
                backdropPath: Vue.config.BACKDROP_PATH,
                imagePath: Vue.config.IMAGE_PATH,
                images: []
            }
        },
        props: {
            data: {
                type: Array,
                default: []
            },
            items: {
                type: Number,
                default: 4
            },
            title: String,
            subtitle: String
        },
        created () {
            this.init()
        },
        methods: {
            init () {
                this.images = []
                this.data.forEach((image) => {
                    this.images.push({
                        thumb: this.imagePath + image.file_path,
                        src: this.backdropPath + image.file_path
                    })
                })
            },
            openGallery (index) {
                this.$refs.lightbox.showImage(index)
            }
        },
        watch: {
            'data': 'init'
        }
    }
</script>

<style lang="scss">
    .vue-lb-box {
        width: 100%;
    }
    .vue-lb-container {
        align-items: center;
        background-color: rgba(0, 0, 0, 0.8);
        box-sizing: border-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        height: 100%;
        justify-content: center;
        left: 0px;
        padding: 10px;
        position: fixed;
        top: 0px;
        width: 100%;
        z-index: 2001;
        -webkit-align-items: center;
        -moz-box-sizing: border-box;
        -webkit-justify-content: center;
        -ms-flex-align: center;
        -webkit-box-align: center;
        -ms-flex-pack: center;
        -webkit-box-pack: center;
    }
    .vue-lb-content {
        margin-bottom: 60px;
        max-width: 1024px;
        position: relative;
    }
    .vue-lb-header {
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        justify-content: space-between;
        height: 40px;
        -webkit-justify-content: space-between;
        -ms-flex-pack: justify;
        -webkit-box-pack: justify;
    }
    .vue-lb-button-close {
        background: none;
        border: none;
        cursor: pointer;
        outline: none;
        position: relative;
        top: 0px;
        vertical-align: bottom;
        height: 40px;
        margin-right: -10px;
        padding: 10px;
        width: 40px;
    }
    .vue-lb-figure {
        margin: 0px;
        display: block;
        position: relative;
    }
    img.vue-lb-modal-image {
        cursor: pointer;
        max-height: calc(100vh - 140px);
        cursor: pointer;
        display: block;
        height: auto;
        margin: 0 auto;
        max-width: 100%;
        user-select: none;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
    }
    .vue-lb-info {
        visibility: initial;
        position: absolute;
        bottom: 25px;
        color: white;
        background-color: rgba(0, 0, 0, 0.7);
        height: 40px;
        width: 100%;
        font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
        text-align: center;
    }
    .vue-lb-footer {
        box-sizing: border-box;
        color: white;
        cursor: auto;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        justify-content: space-between;
        left: 0px;
        line-height: 1.3;
        padding-bottom: 5px;
        padding-left: 0px;
        padding-right: 0px;
        padding-top: 5px;
        -moz-box-sizing: border-box;
        -webkit-justify-content: space-between;
        -ms-flex-pack: justify;
        -webkit-box-pack: justify;
    }
    .vue-lb-footer-info {
        display: block;
        flex: 1 1 0;
        -webkit-flex: 1 1 0;
        -ms-flex: 1 1 0;
    }
    .vue-lb-footer-count {
        color: rgba(255, 255, 255, 0.75);
        font-size: 0.85em;
        padding-left: 1em;
    }
    .vue-lb-thumbnail {
        bottom: 10px;
        height: 50px;
        padding: 0 50px;
        text-align: center;
        white-space: nowrap;
        display: inline-block;
        position: relative;
    }
    .vue-lb-modal-thumbnail {
        background-position: center;
        background-size: cover;
        border-radius: 2px;
        box-shadow: inset 0 0 0 1px hsla(0, 0%, 100%, .2);
        cursor: pointer;
        display: inline-block;
        height: 50px;
        margin: 2px;
        overflow: hidden;
        width: 50px;
    }
    .vue-lb-modal-thumbnail-active {
        background-position: center;
        background-size: cover;
        border-radius: 2px;
        box-shadow: inset 0 0 0 2px white;
        cursor: pointer;
        display: inline-block;
        height: 50px;
        margin: 2px;
        overflow: hidden;
        width: 50px;
    }
    .vue-lb-thumbnail-arrow {
        height: 54px;
        width: 40px;
        background: none;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        outline: none;
        padding: 10px;
        position: absolute;
        top: 50%;
        -webkit-touch-callout: none;
        user-select: none;
        height: 50px;
        margin-top: -25px;
        width: 30px;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
    }
    .vue-lb-thumbnail-left {
        left: 10px;
    }
    .vue-lb-thumbnail-right {
        right: 10px;
    }
    .vue-lb-arrow {
        background: none;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        outline: none;
        padding: 10px;
        position: absolute;
        top: 50%;
        -webkit-touch-callout: none;
        user-select: none;
        height: 120px;
        margin-top: -60px;
        width: 40px;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
    }
    .vue-lb-left {
        left: 10px;
    }
    .vue-lb-right {
        right: 10px;
    }
    .vue-lb-open {
        overflow: hidden;
    }
    .vue-lb-thumbnail-wrapper {
        bottom: 10px;
        height: 50px;
        left: 0;
        margin: 0 auto;
        position: absolute;
        right: 0;
        text-align: center;
        top: auto;
    }
    @media (min-width: 500px) {
        .vue-lb-thumbnail-arrow {
            width: 40px;
        }
    }
    @media (min-width: 768px) {
        .vue-lb-arrow {
            width: 70px;
        }
    }
    .fade-enter-active,
    .fade-leave-active {
        transition: opacity .2s ease;
    }
    .fade-enter,
    .fade-leave-to {
        opacity: 0;
    }
    .light-box {
        padding: 25px 0 30px;
        background-color: #27272E;

        .thumbnails {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;

            .thumbnail {
                flex-basis: 15%;
                text-align: center;
                cursor: pointer;

                img {
                    display: block;
                    width: 100%;
                }
                &.view-more {
                    align-self: center;
                    color: rgba(255, 255, 255, 0.4);
                }
            }
        }
    }
</style>



